import React, { useState, useEffect } from 'react';
import articles from './article-content';
import CommmentsCpn from '../components/CommentsCpn';
import UpvoteBtn from '../components/UpvoteBtn';
import CommentForm from '../components/CommentForm';

export default function Artical(props) {
  const [articalsInfor, setArticalsInfor] = useState({
    upvotes: 0,
    comments: [],
  });

  const name = props.name;
  useEffect(() => {
    const fetchData = async () => {
      const data = await fetch(`/api/articals/${name}`);
      const rs = await data.json();
      setArticalsInfor(rs);
    };

    fetchData();
  }, [name]);
  const contends = articles.find(item => item.name === name);

  return (
    <>
      <h1>Artical Page</h1>
      <p>This post has been upvotes {articalsInfor.upvotes} time </p>
      {contends.content.map((item, key) => (
        <p key={key}> {item} </p>
      ))}

      <CommmentsCpn comments={articalsInfor.comments} />
      <UpvoteBtn
        articalName={name}
        upvotes={articalsInfor.upvotes}
        setArticalsInfor={setArticalsInfor}
      />
      <CommentForm articalName={name} setArticalsInfor={setArticalsInfor} />
    </>
  );
}
