import React from 'react';

export default function UpvoteBtn({ upvotes, articalName, setArticalsInfor }) {
  const addUpvotes = async () => {
    const data = await fetch(`/api/articals/${articalName}/upvote`, {
      method: 'POST',
    });
    const body = await data.json();
    console.log('data', body);
    setArticalsInfor(body);
  };
  return (
    <>
      <button onClick={addUpvotes}>Upvote times</button>
      <p>this is upvote {upvotes} time</p>
    </>
  );
}
